'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import { 
  DialerCampaign, 
  DialerDashboardStats, 
  CampaignStatus,
  DialerMode 
} from '@/lib/power-dialer/types'
import { PageHeader } from '@/components/ui/PageHeader'

// ==========================================
// POWER DIALER MAIN PAGE
// Campaign Management & Dashboard Overview
// ==========================================

export default function PowerDialerPage() {
  // State
  const [activeTab, setActiveTab] = useState<'dashboard' | 'campaigns' | 'active'>('dashboard')
  const [campaigns, setCampaigns] = useState<DialerCampaign[]>([])
  const [dashboardStats, setDashboardStats] = useState<DialerDashboardStats | null>(null)
  const [loading, setLoading] = useState(true)
  const [showNewCampaignModal, setShowNewCampaignModal] = useState(false)
  const [selectedCampaign, setSelectedCampaign] = useState<DialerCampaign | null>(null)
  
  // TODO: Get from auth context
  const subAccountId = 'b0000000-0000-0000-0000-000000000001'

  // Fetch dashboard stats and campaigns
  useEffect(() => {
    fetchDashboardData()
  }, [])

  const fetchDashboardData = async () => {
    setLoading(true)
    try {
      // Fetch stats
      const statsRes = await fetch(`/api/v1/dialer/stats?subAccountId=${subAccountId}&type=dashboard`)
      const statsData = await statsRes.json()
      if (statsData.success) {
        setDashboardStats(statsData.data)
      }

      // Fetch campaigns
      const campaignsRes = await fetch(`/api/v1/dialer/campaigns?subAccountId=${subAccountId}`)
      const campaignsData = await campaignsRes.json()
      if (campaignsData.success) {
        setCampaigns(campaignsData.data)
      }
    } catch (error) {
      console.error('Error fetching dashboard data:', error)
    } finally {
      setLoading(false)
    }
  }

  // Status badge colors
  const getStatusColor = (status: CampaignStatus) => {
    const colors = {
      draft: 'bg-gray-100 text-gray-700',
      active: 'bg-green-100 text-green-700',
      paused: 'bg-yellow-100 text-yellow-700',
      completed: 'bg-blue-100 text-blue-700',
      archived: 'bg-gray-100 text-gray-500'
    }
    return colors[status] || colors.draft
  }

  const formatDuration = (seconds: number) => {
    const hours = Math.floor(seconds / 3600)
    const mins = Math.floor((seconds % 3600) / 60)
    const secs = seconds % 60
    if (hours > 0) return `${hours}h ${mins}m`
    if (mins > 0) return `${mins}m ${secs}s`
    return `${secs}s`
  }

  return (
    <div className="p-6 max-w-7xl mx-auto">
      {/* Header */}
      <PageHeader
        title="Power Dialer"
        description="Manage outbound calling campaigns and track performance"
        icon="fa-phone-volume"
        actions={
          <div className="flex gap-3">
            <Link 
              href="/sub-account/power-dialer/settings"
              className="px-4 py-2 border border-gray-300 rounded-lg text-gray-700 hover:bg-gray-50 flex items-center gap-2"
            >
              <i className="fas fa-cog"></i>
              Settings
            </Link>
            <button 
              onClick={() => setShowNewCampaignModal(true)}
              className="px-4 py-2 bg-[var(--theme-secondary)] text-white rounded-lg hover:opacity-90 flex items-center gap-2"
            >
              <i className="fas fa-plus"></i>
              New Campaign
            </button>
          </div>
        }
      />

      {/* Tabs */}
      <div className="flex gap-1 mb-6 bg-gray-100 p-1 rounded-lg w-fit">
        {[
          { id: 'dashboard', label: 'Dashboard', icon: 'fas fa-chart-line' },
          { id: 'campaigns', label: 'Campaigns', icon: 'fas fa-bullhorn' },
          { id: 'active', label: 'Active Dialer', icon: 'fas fa-headset' }
        ].map(tab => (
          <button
            key={tab.id}
            onClick={() => setActiveTab(tab.id as typeof activeTab)}
            className={`px-4 py-2 rounded-md flex items-center gap-2 transition-colors ${
              activeTab === tab.id 
                ? 'bg-white text-blue-600 shadow-sm' 
                : 'text-gray-600 hover:text-gray-900'
            }`}
          >
            <i className={tab.icon}></i>
            {tab.label}
          </button>
        ))}
      </div>

      {/* Loading State */}
      {loading ? (
        <div className="flex justify-center items-center h-64">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
        </div>
      ) : (
        <>
          {/* Dashboard Tab */}
          {activeTab === 'dashboard' && (
            <DashboardView stats={dashboardStats} campaigns={campaigns} />
          )}

          {/* Campaigns Tab */}
          {activeTab === 'campaigns' && (
            <CampaignsView 
              campaigns={campaigns} 
              onSelectCampaign={setSelectedCampaign}
              getStatusColor={getStatusColor}
              formatDuration={formatDuration}
              onRefresh={fetchDashboardData}
              subAccountId={subAccountId}
            />
          )}

          {/* Active Dialer Tab */}
          {activeTab === 'active' && (
            <ActiveDialerView 
              campaigns={campaigns.filter(c => c.status === 'active')}
              subAccountId={subAccountId}
            />
          )}
        </>
      )}

      {/* New Campaign Modal */}
      {showNewCampaignModal && (
        <NewCampaignModal 
          onClose={() => setShowNewCampaignModal(false)}
          onCreated={() => {
            setShowNewCampaignModal(false)
            fetchDashboardData()
          }}
          subAccountId={subAccountId}
        />
      )}

      {/* Campaign Details Slideout */}
      {selectedCampaign && (
        <CampaignDetailsSlideout
          campaign={selectedCampaign}
          onClose={() => setSelectedCampaign(null)}
          onUpdated={fetchDashboardData}
          subAccountId={subAccountId}
        />
      )}
    </div>
  )
}

// ==========================================
// DASHBOARD VIEW COMPONENT
// ==========================================

function DashboardView({ 
  stats, 
  campaigns 
}: { 
  stats: DialerDashboardStats | null
  campaigns: DialerCampaign[]
}) {
  if (!stats) return null

  const statCards = [
    {
      label: "Today's Calls",
      value: stats.today.totalCalls,
      icon: 'fas fa-phone',
      color: 'blue',
      subtext: `${stats.today.answeredCalls} answered`
    },
    {
      label: 'Connect Rate',
      value: `${stats.today.connectRate}%`,
      icon: 'fas fa-percentage',
      color: 'green',
      subtext: `${stats.today.answeredCalls}/${stats.today.totalCalls} connected`
    },
    {
      label: 'Avg Call Duration',
      value: formatTime(stats.today.avgCallDuration),
      icon: 'fas fa-clock',
      color: 'purple',
      subtext: `${formatTime(stats.today.totalTalkTime)} total`
    },
    {
      label: 'Active Campaigns',
      value: stats.activeCampaigns,
      icon: 'fas fa-bullhorn',
      color: 'orange',
      subtext: `${stats.queuedContacts} contacts queued`
    },
    {
      label: 'Active Agents',
      value: stats.activeAgents,
      icon: 'fas fa-headset',
      color: 'teal',
      subtext: `${stats.callsInProgress} calls in progress`
    },
    {
      label: 'Voicemails',
      value: stats.today.voicemails,
      icon: 'fas fa-voicemail',
      color: 'red',
      subtext: 'Dropped today'
    }
  ]

  function formatTime(seconds: number) {
    const mins = Math.floor(seconds / 60)
    const secs = seconds % 60
    return mins > 0 ? `${mins}m ${secs}s` : `${secs}s`
  }

  return (
    <div className="space-y-6">
      {/* Stats Grid */}
      <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-6 gap-4">
        {statCards.map((stat, index) => (
          <div key={index} className="bg-white rounded-xl shadow-sm border p-4">
            <div className={`w-10 h-10 rounded-lg bg-${stat.color}-100 flex items-center justify-center mb-3`}>
              <i className={`${stat.icon} text-${stat.color}-600`}></i>
            </div>
            <p className="text-2xl font-bold text-gray-900">{stat.value}</p>
            <p className="text-sm text-gray-500">{stat.label}</p>
            <p className="text-xs text-gray-400 mt-1">{stat.subtext}</p>
          </div>
        ))}
      </div>

      {/* Quick Actions */}
      <div className="bg-white rounded-xl shadow-sm border p-6">
        <h3 className="font-semibold text-gray-900 mb-4">Quick Actions</h3>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
          <Link 
            href="/sub-account/power-dialer/dialer"
            className="p-4 border rounded-lg hover:bg-blue-50 hover:border-blue-200 transition-colors text-center"
          >
            <i className="fas fa-headset text-2xl text-blue-600 mb-2"></i>
            <p className="font-medium text-gray-900">Start Dialing</p>
            <p className="text-xs text-gray-500">Launch active dialer</p>
          </Link>
          <Link 
            href="/sub-account/power-dialer/scripts"
            className="p-4 border rounded-lg hover:bg-green-50 hover:border-green-200 transition-colors text-center"
          >
            <i className="fas fa-scroll text-2xl text-green-600 mb-2"></i>
            <p className="font-medium text-gray-900">Call Scripts</p>
            <p className="text-xs text-gray-500">Manage scripts</p>
          </Link>
          <Link 
            href="/sub-account/power-dialer/analytics"
            className="p-4 border rounded-lg hover:bg-purple-50 hover:border-purple-200 transition-colors text-center"
          >
            <i className="fas fa-chart-bar text-2xl text-purple-600 mb-2"></i>
            <p className="font-medium text-gray-900">Analytics</p>
            <p className="text-xs text-gray-500">View reports</p>
          </Link>
          <Link 
            href="/sub-account/power-dialer/settings"
            className="p-4 border rounded-lg hover:bg-orange-50 hover:border-orange-200 transition-colors text-center"
          >
            <i className="fas fa-cog text-2xl text-orange-600 mb-2"></i>
            <p className="font-medium text-gray-900">Settings</p>
            <p className="text-xs text-gray-500">Configure dialer</p>
          </Link>
        </div>
      </div>

      {/* Recent Campaigns */}
      <div className="bg-white rounded-xl shadow-sm border p-6">
        <div className="flex justify-between items-center mb-4">
          <h3 className="font-semibold text-gray-900">Recent Campaigns</h3>
          <Link href="/sub-account/power-dialer?tab=campaigns" className="text-blue-600 text-sm hover:underline">
            View all
          </Link>
        </div>
        <div className="space-y-3">
          {campaigns.slice(0, 5).map(campaign => (
            <div key={campaign.id} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
              <div className="flex items-center gap-3">
                <div className={`w-2 h-2 rounded-full ${
                  campaign.status === 'active' ? 'bg-green-500 animate-pulse' :
                  campaign.status === 'paused' ? 'bg-yellow-500' :
                  'bg-gray-400'
                }`}></div>
                <div>
                  <p className="font-medium text-gray-900">{campaign.name}</p>
                  <p className="text-xs text-gray-500">
                    {campaign.contactsRemaining} contacts remaining
                  </p>
                </div>
              </div>
              <div className="text-right">
                <p className="text-sm font-medium text-gray-900">
                  {campaign.answeredCalls}/{campaign.totalCalls}
                </p>
                <p className="text-xs text-gray-500">
                  {campaign.totalCalls > 0 
                    ? `${Math.round((campaign.answeredCalls / campaign.totalCalls) * 100)}% connect`
                    : '0% connect'
                  }
                </p>
              </div>
            </div>
          ))}
          {campaigns.length === 0 && (
            <div className="text-center py-8 text-gray-500">
              <i className="fas fa-bullhorn text-3xl mb-2 opacity-50"></i>
              <p>No campaigns yet</p>
              <p className="text-sm">Create your first campaign to get started</p>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}

// ==========================================
// CAMPAIGNS VIEW COMPONENT
// ==========================================

function CampaignsView({ 
  campaigns,
  onSelectCampaign,
  getStatusColor,
  formatDuration,
  onRefresh,
  subAccountId
}: { 
  campaigns: DialerCampaign[]
  onSelectCampaign: (campaign: DialerCampaign) => void
  getStatusColor: (status: CampaignStatus) => string
  formatDuration: (seconds: number) => string
  onRefresh: () => void
  subAccountId: string
}) {
  const [filter, setFilter] = useState<CampaignStatus | 'all'>('all')
  const [searchQuery, setSearchQuery] = useState('')

  const filteredCampaigns = campaigns.filter(c => {
    const matchesFilter = filter === 'all' || c.status === filter
    const matchesSearch = c.name.toLowerCase().includes(searchQuery.toLowerCase())
    return matchesFilter && matchesSearch
  })

  const handleStatusChange = async (campaignId: string, newStatus: CampaignStatus) => {
    try {
      const res = await fetch('/api/v1/dialer/campaigns', {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          id: campaignId,
          subAccountId,
          status: newStatus
        })
      })
      if (res.ok) {
        onRefresh()
      }
    } catch (error) {
      console.error('Error updating campaign:', error)
    }
  }

  return (
    <div className="space-y-4">
      {/* Filters */}
      <div className="flex gap-4 items-center">
        <div className="relative flex-1 max-w-md">
          <i className="fas fa-search absolute left-3 top-1/2 -translate-y-1/2 text-gray-400"></i>
          <input
            type="text"
            placeholder="Search campaigns..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="w-full pl-10 pr-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
          />
        </div>
        <select
          value={filter}
          onChange={(e) => setFilter(e.target.value as CampaignStatus | 'all')}
          className="px-4 py-2 border rounded-lg bg-white"
        >
          <option value="all">All Status</option>
          <option value="active">Active</option>
          <option value="paused">Paused</option>
          <option value="draft">Draft</option>
          <option value="completed">Completed</option>
          <option value="archived">Archived</option>
        </select>
      </div>

      {/* Campaigns Table */}
      <div className="bg-white rounded-xl shadow-sm border overflow-hidden">
        <table className="w-full">
          <thead className="bg-gray-50 border-b">
            <tr>
              <th className="text-left px-4 py-3 text-sm font-medium text-gray-600">Campaign</th>
              <th className="text-left px-4 py-3 text-sm font-medium text-gray-600">Status</th>
              <th className="text-left px-4 py-3 text-sm font-medium text-gray-600">Progress</th>
              <th className="text-left px-4 py-3 text-sm font-medium text-gray-600">Performance</th>
              <th className="text-left px-4 py-3 text-sm font-medium text-gray-600">Actions</th>
            </tr>
          </thead>
          <tbody className="divide-y">
            {filteredCampaigns.map(campaign => (
              <tr key={campaign.id} className="hover:bg-gray-50">
                <td className="px-4 py-4">
                  <button 
                    onClick={() => onSelectCampaign(campaign)}
                    className="text-left"
                  >
                    <p className="font-medium text-gray-900 hover:text-blue-600">{campaign.name}</p>
                    <p className="text-sm text-gray-500">{campaign.type} â€¢ {campaign.dialerMode || 'preview'} mode</p>
                  </button>
                </td>
                <td className="px-4 py-4">
                  <span className={`px-2 py-1 rounded-full text-xs font-medium ${getStatusColor(campaign.status)}`}>
                    {campaign.status}
                  </span>
                </td>
                <td className="px-4 py-4">
                  <div className="w-32">
                    <div className="flex justify-between text-xs text-gray-500 mb-1">
                      <span>{campaign.contactsDialed}</span>
                      <span>{campaign.totalContacts}</span>
                    </div>
                    <div className="w-full bg-gray-200 rounded-full h-2">
                      <div 
                        className="bg-blue-600 rounded-full h-2"
                        style={{ 
                          width: `${campaign.totalContacts > 0 
                            ? (campaign.contactsDialed / campaign.totalContacts) * 100 
                            : 0}%` 
                        }}
                      ></div>
                    </div>
                  </div>
                </td>
                <td className="px-4 py-4">
                  <div className="text-sm">
                    <p className="text-gray-900">
                      {campaign.answeredCalls}/{campaign.totalCalls} answered
                    </p>
                    <p className="text-gray-500">
                      Avg: {formatDuration(campaign.avgCallDuration)}
                    </p>
                  </div>
                </td>
                <td className="px-4 py-4">
                  <div className="flex gap-2">
                    {campaign.status === 'active' && (
                      <button 
                        onClick={() => handleStatusChange(campaign.id, 'paused')}
                        className="p-2 text-yellow-600 hover:bg-yellow-50 rounded-lg"
                        title="Pause"
                      >
                        <i className="fas fa-pause"></i>
                      </button>
                    )}
                    {campaign.status === 'paused' && (
                      <button 
                        onClick={() => handleStatusChange(campaign.id, 'active')}
                        className="p-2 text-green-600 hover:bg-green-50 rounded-lg"
                        title="Resume"
                      >
                        <i className="fas fa-play"></i>
                      </button>
                    )}
                    {campaign.status === 'draft' && (
                      <button 
                        onClick={() => handleStatusChange(campaign.id, 'active')}
                        className="p-2 text-green-600 hover:bg-green-50 rounded-lg"
                        title="Start"
                      >
                        <i className="fas fa-play"></i>
                      </button>
                    )}
                    <button 
                      onClick={() => onSelectCampaign(campaign)}
                      className="p-2 text-blue-600 hover:bg-blue-50 rounded-lg"
                      title="View Details"
                    >
                      <i className="fas fa-eye"></i>
                    </button>
                    <Link 
                      href={`/sub-account/power-dialer/dialer?campaign=${campaign.id}`}
                      className="p-2 text-purple-600 hover:bg-purple-50 rounded-lg"
                      title="Open Dialer"
                    >
                      <i className="fas fa-headset"></i>
                    </Link>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        {filteredCampaigns.length === 0 && (
          <div className="text-center py-12 text-gray-500">
            <i className="fas fa-bullhorn text-4xl mb-3 opacity-50"></i>
            <p className="font-medium">No campaigns found</p>
            <p className="text-sm">Try adjusting your filters or create a new campaign</p>
          </div>
        )}
      </div>
    </div>
  )
}

// ==========================================
// ACTIVE DIALER VIEW COMPONENT
// ==========================================

function ActiveDialerView({ 
  campaigns, 
  subAccountId 
}: { 
  campaigns: DialerCampaign[]
  subAccountId: string 
}) {
  const [selectedCampaignId, setSelectedCampaignId] = useState<string | null>(
    campaigns[0]?.id || null
  )

  return (
    <div className="space-y-4">
      {campaigns.length === 0 ? (
        <div className="bg-white rounded-xl shadow-sm border p-12 text-center">
          <i className="fas fa-headset text-5xl text-gray-300 mb-4"></i>
          <h3 className="text-lg font-medium text-gray-900 mb-2">No Active Campaigns</h3>
          <p className="text-gray-500 mb-4">
            You need at least one active campaign to start dialing.
          </p>
          <Link 
            href="/sub-account/power-dialer?tab=campaigns"
            className="inline-flex items-center gap-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
          >
            <i className="fas fa-bullhorn"></i>
            Go to Campaigns
          </Link>
        </div>
      ) : (
        <>
          {/* Campaign Selector */}
          <div className="bg-white rounded-xl shadow-sm border p-4">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Select Campaign to Dial
            </label>
            <select
              value={selectedCampaignId || ''}
              onChange={(e) => setSelectedCampaignId(e.target.value)}
              className="w-full md:w-auto px-4 py-2 border rounded-lg bg-white"
            >
              {campaigns.map(c => (
                <option key={c.id} value={c.id}>
                  {c.name} ({c.contactsRemaining} remaining)
                </option>
              ))}
            </select>
          </div>

          {/* Launch Dialer */}
          <div className="bg-gradient-to-r from-blue-600 to-blue-700 rounded-xl shadow-sm p-8 text-center text-white">
            <i className="fas fa-headset text-5xl mb-4"></i>
            <h3 className="text-2xl font-bold mb-2">Ready to Dial</h3>
            <p className="text-blue-100 mb-6">
              {campaigns.find(c => c.id === selectedCampaignId)?.contactsRemaining || 0} contacts waiting in queue
            </p>
            <Link
              href={`/sub-account/power-dialer/dialer?campaign=${selectedCampaignId}`}
              className="inline-flex items-center gap-2 px-6 py-3 bg-white text-blue-600 rounded-lg font-medium hover:bg-blue-50 transition-colors"
            >
              <i className="fas fa-phone-volume"></i>
              Launch Dialer
            </Link>
          </div>
        </>
      )}
    </div>
  )
}

// ==========================================
// NEW CAMPAIGN MODAL
// ==========================================

function NewCampaignModal({ 
  onClose, 
  onCreated,
  subAccountId 
}: { 
  onClose: () => void
  onCreated: () => void
  subAccountId: string
}) {
  const [formData, setFormData] = useState({
    name: '',
    description: '',
    type: 'outbound' as 'outbound' | 'callback' | 'follow_up',
    dialerMode: 'preview' as DialerMode,
    priority: 5
  })
  const [saving, setSaving] = useState(false)
  const [error, setError] = useState('')

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!formData.name.trim()) {
      setError('Campaign name is required')
      return
    }

    setSaving(true)
    setError('')

    try {
      const res = await fetch('/api/v1/dialer/campaigns', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          subAccountId,
          ...formData
        })
      })

      const data = await res.json()
      if (data.success) {
        onCreated()
      } else {
        setError(data.error || 'Failed to create campaign')
      }
    } catch (err) {
      setError('Network error')
    } finally {
      setSaving(false)
    }
  }

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center">
      <div className="absolute inset-0 bg-black/50" onClick={onClose}></div>
      <div className="relative bg-white rounded-2xl shadow-xl w-full max-w-lg mx-4 max-h-[90vh] overflow-y-auto">
        <div className="p-6">
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-xl font-bold text-gray-900">New Campaign</h2>
            <button onClick={onClose} className="text-gray-400 hover:text-gray-600">
              <i className="fas fa-times text-xl"></i>
            </button>
          </div>

          {error && (
            <div className="mb-4 p-3 bg-red-50 border border-red-200 rounded-lg text-red-700 text-sm">
              {error}
            </div>
          )}

          <form onSubmit={handleSubmit} className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Campaign Name *
              </label>
              <input
                type="text"
                value={formData.name}
                onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500"
                placeholder="e.g., Q4 Sales Outreach"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Description
              </label>
              <textarea
                value={formData.description}
                onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500"
                rows={3}
                placeholder="Campaign description..."
              />
            </div>

            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Campaign Type
                </label>
                <select
                  value={formData.type}
                  onChange={(e) => setFormData({ ...formData, type: e.target.value as typeof formData.type })}
                  className="w-full px-4 py-2 border rounded-lg bg-white"
                >
                  <option value="outbound">Outbound</option>
                  <option value="callback">Callback</option>
                  <option value="follow_up">Follow Up</option>
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Dialer Mode
                </label>
                <select
                  value={formData.dialerMode}
                  onChange={(e) => setFormData({ ...formData, dialerMode: e.target.value as DialerMode })}
                  className="w-full px-4 py-2 border rounded-lg bg-white"
                >
                  <option value="preview">Preview</option>
                  <option value="progressive">Progressive</option>
                  <option value="predictive">Predictive</option>
                </select>
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Priority (1-10)
              </label>
              <input
                type="range"
                min="1"
                max="10"
                value={formData.priority}
                onChange={(e) => setFormData({ ...formData, priority: parseInt(e.target.value) })}
                className="w-full"
              />
              <div className="flex justify-between text-xs text-gray-500">
                <span>Low</span>
                <span>{formData.priority}</span>
                <span>High</span>
              </div>
            </div>

            <div className="flex gap-3 pt-4">
              <button
                type="button"
                onClick={onClose}
                className="flex-1 px-4 py-2 border rounded-lg text-gray-700 hover:bg-gray-50"
              >
                Cancel
              </button>
              <button
                type="submit"
                disabled={saving}
                className="flex-1 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50 flex items-center justify-center gap-2"
              >
                {saving ? (
                  <>
                    <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin"></div>
                    Creating...
                  </>
                ) : (
                  <>
                    <i className="fas fa-plus"></i>
                    Create Campaign
                  </>
                )}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}

// ==========================================
// CAMPAIGN DETAILS SLIDEOUT
// ==========================================

function CampaignDetailsSlideout({ 
  campaign, 
  onClose,
  onUpdated,
  subAccountId
}: { 
  campaign: DialerCampaign
  onClose: () => void
  onUpdated: () => void
  subAccountId: string
}) {
  const [activeSection, setActiveSection] = useState<'overview' | 'contacts' | 'stats'>('overview')

  const connectRate = campaign.totalCalls > 0 
    ? Math.round((campaign.answeredCalls / campaign.totalCalls) * 100)
    : 0

  return (
    <div className="fixed inset-0 z-50">
      <div className="absolute inset-0 bg-black/30" onClick={onClose}></div>
      <div className="absolute right-0 top-0 bottom-0 w-full max-w-2xl bg-white shadow-2xl overflow-y-auto">
        {/* Header */}
        <div className="sticky top-0 bg-white border-b p-4 flex justify-between items-center z-10">
          <div>
            <h2 className="text-xl font-bold text-gray-900">{campaign.name}</h2>
            <p className="text-sm text-gray-500">{campaign.type} campaign</p>
          </div>
          <button onClick={onClose} className="p-2 text-gray-400 hover:text-gray-600">
            <i className="fas fa-times text-xl"></i>
          </button>
        </div>

        {/* Section Tabs */}
        <div className="border-b px-4">
          <div className="flex gap-6">
            {[
              { id: 'overview', label: 'Overview' },
              { id: 'contacts', label: 'Contacts' },
              { id: 'stats', label: 'Statistics' }
            ].map(tab => (
              <button
                key={tab.id}
                onClick={() => setActiveSection(tab.id as typeof activeSection)}
                className={`py-3 border-b-2 transition-colors ${
                  activeSection === tab.id
                    ? 'border-blue-600 text-blue-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700'
                }`}
              >
                {tab.label}
              </button>
            ))}
          </div>
        </div>

        {/* Content */}
        <div className="p-6">
          {activeSection === 'overview' && (
            <div className="space-y-6">
              {/* Status & Actions */}
              <div className="flex gap-4 items-center">
                <span className={`px-3 py-1 rounded-full text-sm font-medium ${
                  campaign.status === 'active' ? 'bg-green-100 text-green-700' :
                  campaign.status === 'paused' ? 'bg-yellow-100 text-yellow-700' :
                  'bg-gray-100 text-gray-700'
                }`}>
                  {campaign.status.toUpperCase()}
                </span>
                <Link
                  href={`/sub-account/power-dialer/dialer?campaign=${campaign.id}`}
                  className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 flex items-center gap-2"
                >
                  <i className="fas fa-headset"></i>
                  Open Dialer
                </Link>
              </div>

              {/* Description */}
              {campaign.description && (
                <div>
                  <h4 className="text-sm font-medium text-gray-500 mb-1">Description</h4>
                  <p className="text-gray-700">{campaign.description}</p>
                </div>
              )}

              {/* Progress */}
              <div>
                <h4 className="text-sm font-medium text-gray-500 mb-2">Progress</h4>
                <div className="bg-gray-100 rounded-lg p-4">
                  <div className="flex justify-between mb-2">
                    <span className="text-sm text-gray-600">Contacts Dialed</span>
                    <span className="font-medium">{campaign.contactsDialed} / {campaign.totalContacts}</span>
                  </div>
                  <div className="w-full bg-gray-200 rounded-full h-3">
                    <div 
                      className="bg-blue-600 rounded-full h-3"
                      style={{ width: `${campaign.totalContacts > 0 ? (campaign.contactsDialed / campaign.totalContacts) * 100 : 0}%` }}
                    ></div>
                  </div>
                  <p className="text-sm text-gray-500 mt-2">
                    {campaign.contactsRemaining} contacts remaining
                  </p>
                </div>
              </div>

              {/* Key Metrics */}
              <div>
                <h4 className="text-sm font-medium text-gray-500 mb-2">Key Metrics</h4>
                <div className="grid grid-cols-2 gap-4">
                  <div className="bg-gray-50 rounded-lg p-4">
                    <p className="text-2xl font-bold text-gray-900">{campaign.totalCalls}</p>
                    <p className="text-sm text-gray-500">Total Calls</p>
                  </div>
                  <div className="bg-gray-50 rounded-lg p-4">
                    <p className="text-2xl font-bold text-green-600">{connectRate}%</p>
                    <p className="text-sm text-gray-500">Connect Rate</p>
                  </div>
                  <div className="bg-gray-50 rounded-lg p-4">
                    <p className="text-2xl font-bold text-gray-900">{campaign.answeredCalls}</p>
                    <p className="text-sm text-gray-500">Answered</p>
                  </div>
                  <div className="bg-gray-50 rounded-lg p-4">
                    <p className="text-2xl font-bold text-gray-900">{campaign.voicemails}</p>
                    <p className="text-sm text-gray-500">Voicemails</p>
                  </div>
                </div>
              </div>

              {/* Settings */}
              <div>
                <h4 className="text-sm font-medium text-gray-500 mb-2">Settings</h4>
                <div className="bg-gray-50 rounded-lg p-4 space-y-2">
                  <div className="flex justify-between">
                    <span className="text-gray-600">Dialer Mode</span>
                    <span className="font-medium capitalize">{campaign.dialerMode || 'preview'}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-600">Priority</span>
                    <span className="font-medium">{campaign.priority}/10</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-600">Type</span>
                    <span className="font-medium capitalize">{campaign.type}</span>
                  </div>
                </div>
              </div>
            </div>
          )}

          {activeSection === 'contacts' && (
            <div className="text-center py-12 text-gray-500">
              <i className="fas fa-users text-4xl mb-3 opacity-50"></i>
              <p>Contact list management</p>
              <p className="text-sm">Coming soon - import and manage contacts</p>
            </div>
          )}

          {activeSection === 'stats' && (
            <div className="space-y-6">
              {/* Call Breakdown */}
              <div>
                <h4 className="text-sm font-medium text-gray-500 mb-2">Call Breakdown</h4>
                <div className="space-y-3">
                  <StatBar label="Answered" value={campaign.answeredCalls} total={campaign.totalCalls} color="green" />
                  <StatBar label="Voicemails" value={campaign.voicemails} total={campaign.totalCalls} color="blue" />
                  <StatBar label="No Answer" value={campaign.noAnswers} total={campaign.totalCalls} color="yellow" />
                  <StatBar label="Busy" value={campaign.busySignals} total={campaign.totalCalls} color="orange" />
                  <StatBar label="Failed" value={campaign.failedCalls} total={campaign.totalCalls} color="red" />
                </div>
              </div>

              {/* Time Stats */}
              <div>
                <h4 className="text-sm font-medium text-gray-500 mb-2">Time Statistics</h4>
                <div className="grid grid-cols-2 gap-4">
                  <div className="bg-gray-50 rounded-lg p-4">
                    <p className="text-xl font-bold text-gray-900">
                      {formatTime(campaign.avgCallDuration)}
                    </p>
                    <p className="text-sm text-gray-500">Avg Call Duration</p>
                  </div>
                  <div className="bg-gray-50 rounded-lg p-4">
                    <p className="text-xl font-bold text-gray-900">
                      {formatTime(campaign.totalTalkTime)}
                    </p>
                    <p className="text-sm text-gray-500">Total Talk Time</p>
                  </div>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}

// Helper component for stat bars
function StatBar({ 
  label, 
  value, 
  total, 
  color 
}: { 
  label: string
  value: number
  total: number
  color: string
}) {
  const percentage = total > 0 ? (value / total) * 100 : 0
  const colorClasses = {
    green: 'bg-green-500',
    blue: 'bg-blue-500',
    yellow: 'bg-yellow-500',
    orange: 'bg-orange-500',
    red: 'bg-red-500'
  }

  return (
    <div>
      <div className="flex justify-between text-sm mb-1">
        <span className="text-gray-600">{label}</span>
        <span className="font-medium">{value} ({percentage.toFixed(1)}%)</span>
      </div>
      <div className="w-full bg-gray-200 rounded-full h-2">
        <div 
          className={`${colorClasses[color as keyof typeof colorClasses]} rounded-full h-2`}
          style={{ width: `${percentage}%` }}
        ></div>
      </div>
    </div>
  )
}

// Helper function
function formatTime(seconds: number) {
  const hours = Math.floor(seconds / 3600)
  const mins = Math.floor((seconds % 3600) / 60)
  const secs = seconds % 60
  if (hours > 0) return `${hours}h ${mins}m`
  if (mins > 0) return `${mins}m ${secs}s`
  return `${secs}s`
}
